.edit_container {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.contents_container {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-top: 3%;

    border-top-left-radius: 30px;
    border-top-right-radius: 30px;

    height: 90%;
    width: 90%;
    background-color: #E3EDF9;

    overflow-y: auto;
}

.contents_container button {

}

.edit_components {
    display: flex;
    flex-direction: column;
    width: 90%;
}

.edit_component {
    display: flex;
    flex-direction: column;

    width: 70%;

    margin-top: 5%;
}

.small_input_box {
    margin-top: 1%;
    width: 120%;
    height: 6vh;
    border-radius: 4px;
    border: 0.5px solid grey;
    padding: 8px;
}

.big_input_box {
    margin-top: 1%;
    height: 24vh;
    width: 140%;
    border-radius: 4px;
    border: 0.5px solid grey;
    padding: 8px;
    resize: None;
}

.API_edit {
    width: 10%;
    height: 2vw;
    background-color: #3563E9;
    color: white;
    margin-bottom: 5%;
    border-radius: 4px;
    border-width: 0;

    font-size: 20px;
}

.circle-button {
    margin-top : 3%;
    margin-bottom: 10%;
    background-color: #3563E9;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    cursor: pointer;
    font-size: 16px;
}